.card {
  box-sizing: border-box;
  width: 200px;
  padding: 16px;
  font-family: Roboto, sans-serif;
}

.card__product-image {
  width: 160px;
  height: 134px;
  padding: 16px 3px 0;
  display: flex;
}

.card__product-name {
  color: #060b35;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__product-rating {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.card__product-rating-review {
  color: #060b35;
  align-self: flex-end;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__product-price {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__product-price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.card__product-price-numbers {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__product-buy-link {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__product-buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
